import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { Search } from "lucide-react";
import "./styles/SearchBar.css";

const SearchBar = ({ token, apiUrl, user }) => {
  const [searchType, setSearchType] = useState("all");
  const [searchQuery, setSearchQuery] = useState("");
  const [showFilterOptions, setShowFilterOptions] = useState(false);
  const [searchResults, setSearchResults] = useState([]);

  const handleSearch = async () => {
    if (!searchQuery.trim()) return;
    const query = encodeURIComponent(searchQuery.trim());
    try {
      let endpoint = "";
      switch (searchType) {
        case "users":
          endpoint = `${apiUrl}/api/users/search?query=${query}`;
          break;
        case "news":
          endpoint = `${apiUrl}/api/news/search?query=${query}`;
          break;
        case "groups":
          endpoint = `${apiUrl}/api/groups/search?query=${query}`;
          break;
        case "homework":
          endpoint = `${apiUrl}/api/homework/search?query=${query}`;
          break;
        default:
          endpoint = `${apiUrl}/api/search?query=${query}`;
      }

      const res = await axios.get(endpoint, {
        headers: { Authorization: `Bearer ${token}` },
      });

      if (searchType === "all") {
        const { users, news, groups, homework } = res.data;
        const combined = [
          ...users.map((u) => ({ ...u, type: "user" })),
          ...news.map((n) => ({ ...n, type: "news" })),
          ...groups.map((g) => ({ ...g, type: "group" })),
          ...homework.map((h) => ({ ...h, type: "homework" })),
        ];
        setSearchResults(combined);
      } else {
        const typedData = res.data.map((item) => ({
          ...item,
          type:
            searchType === "users"
              ? "user"
              : searchType === "news"
              ? "news"
              : searchType === "groups"
              ? "group"
              : searchType === "homework"
              ? "homework"
              : "unknown",
        }));
        setSearchResults(typedData);
      }
    } catch (err) {
      console.error("Error searching:", err);
    }
  };

  return (
    <div className="search-bar-container">
      <div
        className="search-filter"
        onClick={() => setShowFilterOptions(!showFilterOptions)}
      >
        {searchType === "all" && ""}
        {searchType === "users" && ""}
        {searchType === "news" && ""}
        {searchType === "groups" && ""}
        {searchType === "homework" && ""}
        <span className="arrow"></span>
        {showFilterOptions && (
          <div className="filter-dropdown">
            <div onClick={() => setSearchType("all")}></div>
            <div onClick={() => setSearchType("users")}></div>
            <div onClick={() => setSearchType("news")}></div>
            <div onClick={() => setSearchType("groups")}></div>
            <div onClick={() => setSearchType("homework")}></div>
          </div>
        )}
      </div>

      <input
        type="text"
        placeholder="..."
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
        className="search-input"
        onKeyDown={(e) => e.key === "Enter" && handleSearch()}
      />
      <button onClick={handleSearch} style={{ padding: "9px 10px" }}>
        <Search color="black" height={"15px"} />
      </button>

      <div
        className="clear-button"
        onClick={() => {
          setSearchQuery("");
          setSearchResults([]);
        }}
      >
        
      </div>

      {searchResults && searchResults.length > 0 && (
        <div className="search-results-count">
          : <span>{searchResults.length}</span> 
          <div className="search-results">
            {searchResults.map((result) => (
              <div key={result._id} className="search-result-item">
                {/*      */}

                {result.type === "user" ? (
                  <Link to={`/profile/${result._id}`}>
                    <p className="search-result-item-link">
                       {result.username}
                    </p>
                  </Link>
                ) : result.type === "news" ? (
                  <Link to={`/news?highlight=${result._id}`}>
                     {result.title}
                  </Link>
                ) : result.type === "group" ? (
                  <Link to={`/groups?highlight=${result._id}`}>
                    <p> {result.name}</p>
                  </Link>
                ) : result.type === "homework" ? (
                  <Link
                    to={
                      user.role === "student"
                        ? `/homework?highlight=${result._id}`
                        : `/homework?highlight=${result._id}`
                    }
                  >
                    <p>
                       {result.subject} {" "}
                      {new Date(result.date).toLocaleDateString()}
                    </p>
                  </Link>
                ) : null}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default SearchBar;
