import React, { useState, useEffect, useRef, forwardRef } from "react";
import axios from "axios";
import "./styles/TwoFAVerifyModal.css";

const TwoFAVerifyModal = forwardRef(
  (
    { apiUrl, userId, onSuccess, onClose, showQRCode, qrCodeUrl, mode },
    ref
  ) => {
    const [code, setCode] = useState(["", "", "", "", "", ""]);
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(false);
    const [success, setSuccess] = useState(false);
    const inputs = useRef([]);

    useEffect(() => {
      if (inputs.current[0]) inputs.current[0].focus();
    }, []);

    const handleChange = (value, index) => {
      if (!/^[0-9]?$/.test(value)) return;
      const newCode = [...code];
      newCode[index] = value;
      setCode(newCode);
      if (value && index < 5) inputs.current[index + 1].focus();
    };

    useEffect(() => {
      const fullCode = code.join("");
      if (fullCode.length === 6) {
        handleSubmit();
      }
    }, [code]);

    const handleKeyDown = (e, index) => {
      if (e.key === "Backspace" && !code[index] && index > 0) {
        inputs.current[index - 1].focus();
      }
    };

    const handleSubmit = async () => {
      const fullCode = code.join("");
      if (fullCode.length !== 6) return setError(" 6- ");
      setLoading(true);
      try {
        const res = await axios.post(`${apiUrl}/api/verify-2fa`, {
          userId,
          token: fullCode,
          mode,
        });

        const isSuccess =
          mode === "verify-only" ? res.data.success : res.data.token;

        if (isSuccess) {
          setSuccess(true);
          setError(null);

          //     
          if (res.data.token) {
            localStorage.setItem("token", res.data.token);
            localStorage.setItem("user", JSON.stringify(res.data.user));
          }

          setTimeout(() => {
            onSuccess(res.data.token, res.data.user);
          }, 600);
        } else {
          setError(" ");
          setSuccess(false);
        }
      } catch (err) {
        console.error("   2FA:", err);
        setError("   ");
      } finally {
        setLoading(false);
      }
    };

    const handlePaste = (e) => {
      const paste = e.clipboardData.getData("text").trim();
      if (/^\d{6}$/.test(paste)) {
        const digits = paste.split("");

        digits.forEach((digit, index) => {
          if (inputs.current[index]) {
            inputs.current[index].value = digit;
          }
        });

        setCode(digits);

        setTimeout(() => {
          handleSubmit(digits.join(""));
        }, 100);

        e.preventDefault();
      }
    };

    return (
      <div className="twofa-modal" ref={ref}>
        <div className="twofa-content">
          {showQRCode && qrCodeUrl && (
            <div className="qrcode-container">
              <small>
                 qr-   Google Auth, Apple Password,
                ...
              </small>
              <img
                src={qrCodeUrl}
                alt="QR-  2FA"
                className="twofa-qrcode"
              />
            </div>
          )}
          <h3>  </h3>
          <small style={{ color: "gray" }}>
            Google Auth, Apple Password, ect...
          </small>
          <div className="twofa-inputs" onPaste={handlePaste}>
            {code.map((digit, index) => (
              <input
                type="tel"
                pattern="[0-9]*"
                value={digit}
                onChange={(e) => handleChange(e.target.value, index)}
                onKeyDown={(e) => handleKeyDown(e, index)}
                ref={(el) => (inputs.current[index] = el)}
                key={index}
                className={success ? "success" : error ? "error" : ""}
                maxLength={1}
              />
            ))}
          </div>
          {error && <p className="twofa-error">{error}</p>}
          <div className="buttons">
            <button className="cancel-auth-btn" onClick={onClose}>
              
            </button>
            <button
              className="confirm-button"
              onClick={handleSubmit}
              disabled={loading}
            >
              {loading ? "..." : ""}
            </button>
          </div>
        </div>
      </div>
    );
  }
);

export default TwoFAVerifyModal;
