import React, { useState, useEffect } from "react";
import GroupEditor from "./GroupEditor";
// import "./GroupDetail.css";

function GroupDetail({ group, token, user, apiUrl }) {
  const [localGroup, setLocalGroup] = useState(group);
  const [editing, setEditing] = useState(false);

  useEffect(() => {
    setLocalGroup(group);
  }, [group]);

  //    ""
  const handleEditClick = () => {
    setEditing(true);
  };

  const handleEditorClose = (updatedGroup) => {
    setLocalGroup(updatedGroup);
    setEditing(false);
  };

  return (
    <div className="group-detail">
      <h3>  : {localGroup.name}</h3>

      {!editing ? (
        <>
          {/*        */}
          {localGroup.schedule && localGroup.schedule.length > 0 ? (
            localGroup.schedule.map((day, dayIndex) => (
              <div key={dayIndex} className="day-schedule">
                <h4>{day.day}</h4>
                <table className="schedule-table">
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {day.lessons && day.lessons.length > 0 ? (
                      day.lessons.map((lesson, lessonIndex) => (
                        <tr key={lessonIndex}>
                          <td>{lesson.time}</td>
                          <td>{lesson.subject}</td>
                          <td>{lesson.teacher}</td>
                          <td>{lesson.room}</td>
                        </tr>
                      ))
                    ) : (
                      <tr>
                        <td colSpan="4"> </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            ))
          ) : (
            <p>  </p>
          )}

          {(user.role === "teacher" || user.role === "director") && (
            <button onClick={handleEditClick}></button>
          )}
        </>
      ) : (
        <GroupEditor
          group={localGroup}
          token={token}
          apiUrl={apiUrl}
          onEditorClose={handleEditorClose}
        />
      )}
    </div>
  );
}

export default GroupDetail;
