import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "axios";
import Loading from "./Loading";
import TwoFAVerifyModal from "./TwoFAVerifyModal";
import PasswordConfirmModal from "./PasswordConfirmModal";
import DelReqModal from "./DelReqModal";
import "./styles/Settings.css";

const Settings = ({ token, apiUrl, user, theme, setTheme }) => {
  const { id } = useParams();
  const [stage, setStage] = useState("idle"); // "password" | "2fa" | "confirm"
  const [password, setPassword] = useState("");
  const [twoFAEnabled, setTwoFAEnabled] = useState(false);
  const [loading, setLoading] = useState(true);
  const [qrCodeUrl, setQRCodeUrl] = useState(null);
  const [secret, setSecret] = useState(null);
  const [code, setCode] = useState("");
  const [verifying, setVerifying] = useState(false);
  const [error, setError] = useState("");
  const [showDisableModal, setShowDisableModal] = useState(false);
  const [showEnableModal, setShowEnableModal] = useState(false);
  const [allowComments, setAllowComments] = useState(false);
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [editingEmail, setEditingEmail] = useState(false);
  const [editingPhone, setEditingPhone] = useState(false);
  const [showAccountDeletionModal, setShowAccountDeletionModal] =
    useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchSettings = async () => {
      try {
        const res = await axios.get(`${apiUrl}/api/users/settings`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setTwoFAEnabled(res.data.twoFAEnabled);
        setAllowComments(res.data.allowComments);
        setEmail(res.data.email || "");
        setPhone(res.data.phone || "");
      } catch (err) {
        console.error("   :", err);
      } finally {
        setLoading(false);
      }
    };

    if (token) {
      fetchSettings();
    }
  }, [token, apiUrl]);

  const toggle2FA = async () => {
    if (twoFAEnabled) {
      setShowDisableModal(true);
      return;
    }

    try {
      const res = await axios.post(
        `${apiUrl}/api/users/2fa/setup`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setQRCodeUrl(res.data.qrCodeUrl);
      setSecret(res.data.secret);
      setTwoFAEnabled(res.data.twoFAEnabled);
      setShowEnableModal(true);
    } catch (err) {
      console.error("   2FA:", err);
      setError("   2FA");
    }
  };

  const disable2FA = async () => {
    try {
      await axios.post(
        `${apiUrl}/api/users/2fa/disable`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setTwoFAEnabled(false);
    } catch (err) {
      console.error("   2FA:", err);
      setError("   2FA");
    }
  };

  const handleContactSave = async (field) => {
    try {
      await axios.put(
        `${apiUrl}/api/users/${user._id}/update-contacts`,
        {
          email: field === "email" ? email : user.email,
          phone: field === "phone" ? phone : user.phone,
        },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      toast.success("  ");

      if (field === "email") setEditingEmail(false);
      if (field === "phone") setEditingPhone(false);
    } catch (err) {
      toast.error("   ");
      console.log("   ", err);
    }
  };

  const handleDeleteClick = () => {
    setStage("password");
  };

  const handleDeleteRequestSend = async (reason) => {
    try {
      await axios.post(
        `${apiUrl}/api/deletion`,
        {
          reason,
        },
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      toast.success("    ");
    } catch (err) {
      toast.error("   ");
    }
  };

  if (loading)
    return (
      <div>
        <Loading />
      </div>
    );

  return (
    <div style={{ padding: "20px" }} className="settings-container">
      <h2></h2>

      <button onClick={() => navigate(-1)} className="back-button">
         
      </button>

      {/*   */}
      <div className="contacts-settings">
        <h3> </h3>

        <div className="contact-field">
          <label>Email:</label>
          {editingEmail ? (
            <>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder={user.email || "example@example.com"}
              />
              <button onClick={() => handleContactSave("email")}>
                 
              </button>
            </>
          ) : (
            <>
              <span>{email || " "}</span>
              <button onClick={() => setEditingEmail(true)}>
                 
              </button>
            </>
          )}
        </div>

        <div className="contact-field">
          <label>:</label>
          {editingPhone ? (
            <>
              <input
                type="tel"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                placeholder={user.phone || "+7 (XXX) XXX-XX-XX"}
              />
              <button onClick={() => handleContactSave("phone")}>
                 
              </button>
            </>
          ) : (
            <>
              <span>{phone || " "}</span>
              <button onClick={() => setEditingPhone(true)}>
                 
              </button>
            </>
          )}
        </div>
      </div>

      {/*   */}
      <div style={{ marginTop: "20px" }}>
        <h4>  (2FA)</h4>

        {twoFAEnabled ? (
          <p style={{ color: "green", fontWeight: "bold" }}>2FA  </p>
        ) : (
          <p style={{ color: "red", fontWeight: "bold" }}>2FA  </p>
        )}

        <button
          onClick={() =>
            twoFAEnabled ? setShowDisableModal(true) : toggle2FA()
          }
          style={{ marginTop: "10px" }}
        >
          {twoFAEnabled ? " 2FA" : " 2FA"}
        </button>

        {error && <p style={{ color: "red" }}>{error}</p>}
      </div>

      <div className="theme-toggle-setting">
        <select value={theme} onChange={(e) => setTheme(e.target.value)}>
          <option value="default-light"></option>
          <option value="default-dark"></option>
          <option value="default-ultra-dark"></option>
        </select>
      </div>

      <div className="comments-toggle-setting">
        <label className="switch">
          <input
            type="checkbox"
            checked={allowComments}
            onChange={async (e) => {
              const newVal = e.target.checked;
              setAllowComments(newVal);
              try {
                await axios.put(
                  `${apiUrl}/api/users/${user._id}/update-comments-setting`,
                  {
                    allowComments: newVal,
                  },
                  {
                    headers: { Authorization: `Bearer ${token}` },
                  }
                );
              } catch (err) {
                console.error(
                  "    :",
                  err
                );
              }
            }}
          />
          <span className="slider"></span>
          <span className="switch-label">
               
          </span>
        </label>
      </div>

      <div className="account-deletion">
        <button onClick={handleDeleteClick}> </button>
      </div>

      {/* ===================================================================================================== */}

      {/*  1:   */}
      {stage === "password" && (
        <PasswordConfirmModal
          onClose={() => setStage("idle")}
          onConfirm={(enteredPassword) => {
            setPassword(enteredPassword);
            if (twoFAEnabled) {
              setStage("2fa");
            } else {
              setStage("confirm");
            }
          }}
        />
      )}

      {/*  2: 2FA ( ) */}
      {stage === "2fa" && (
        <TwoFAVerifyModal
          mode=""
          apiUrl={apiUrl}
          userId={user._id}
          showQRCode={false}
          onClose={() => setStage("idle")}
          onSuccess={() => setStage("confirm")}
        />
      )}

      {/*  3:    */}
      {stage === "confirm" && (
        <DelReqModal
          isOpen={true}
          onClose={() => setStage("idle")}
          onSubmit={handleDeleteRequestSend}
        />
      )}

      {/*    */}
      {showEnableModal && qrCodeUrl && (
        <TwoFAVerifyModal
          mode="verify-only"
          apiUrl={apiUrl}
          userId={user._id}
          showQRCode={true}
          qrCodeUrl={qrCodeUrl}
          onClose={() => setShowEnableModal(false)}
          onSuccess={() => {
            setTwoFAEnabled(true);
            setQRCodeUrl(null);
            setShowEnableModal(false);
          }}
        />
      )}

      {/*    */}
      {showDisableModal && (
        <TwoFAVerifyModal
          mode=""
          apiUrl={apiUrl}
          userId={user._id}
          showQRCode={false}
          onClose={() => setShowDisableModal(false)}
          onSuccess={() => {
            disable2FA();
            setShowDisableModal(false);
          }}
        />
      )}
    </div>
  );
};

export default Settings;
